import React, { Component } from 'react';
import RTbreathetable from './RTbreathetable'
import { DatePicker } from 'antd';
import * as FilterReg from '../../Function/FiliterReg'
import moment from 'moment';

class RTbreathe extends Component {
    state = {
        DateRange: {}
    }

    onDateRangeChange = (date, dateString) => {
        let range = { 'st': date[0].valueOf(), 'et': date[1].valueOf() }
        this.setState({
            DateRange: range
        })
    }

    createRTbreathetable = () => {
        const { searchbar, PickerDate, QSPE, caseinfo } = this.props
        console.log(searchbar)
        console.log(PickerDate)

        const et = searchbar ? this.state.DateRange.et : PickerDate + (31 * 60 * 60 * 1000)
        const st = searchbar ? this.state.DateRange.st : et - (3 * 24 * 60 * 60 * 1000)

        let DataList = FilterReg.QSPERegList(QSPE)
        DataList = DataList.filter(data => {
            let time = moment(data.Date + data.Time, "YYYYMMDDHHmm").valueOf()
            return st <= time && time <= et
        })
        console.log(DataList)
        return <RTbreathetable DataList={DataList} caseinfo={caseinfo}></RTbreathetable>
    }

    render() {
        const { searchbar } = this.props
        const { RangePicker } = DatePicker;
        // const { PickerDate, QSPE, caseinfo } = this.props

        return (
            <div style={{ marginBottom: "20px" }}>
                {/* <div style={{ display: 'flex', alignItems: 'center', height: '50px', padding: "0px 5px", background: "#cccccc" }}> */}
                <t1>
                    呼吸器設定紀錄
                    </t1>
                <div style={{ display: "grid", gridTemplateColumns: "220px 1fr 25px", gridColumnGap: "10px" }}>
                    {searchbar ? <RangePicker placeholder={["請輸入日期", "請輸入日期"]} onChange={this.onDateRangeChange} allowClear={false} /> : null}
                    {/* <Input id="nurtion_feeding_input" placeholder="請輸入關鍵字" /> */}
                    {/* <div onClick={() => this.search_click()} style={{ display: "flex", alignItems: "center" }}>
                        <img src={searchlogo} alt="searchlogo" />
                    </div> */}
                </div>
                {/* </div> */}
                {this.createRTbreathetable()}
            </div>
        );
    }
}

export default RTbreathe
