import React, { Component } from 'react';
import Nonlabviewtable from './Nonlabviewtable'
import { Modal } from 'antd';
import InsReportModal from '../ExamReportModal'


class Nonlabviewlayout extends Component {

    state = {
        modalVisible: false,
        modalReport: ""
    }

    showModal = (report) => {
        console.log("showModal :" + JSON.stringify(report))
        this.setState({
            modalVisible: true,
            modalReport: report
        });
    };

    handleOk = e => {
        this.setState({
            modalVisible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            modalVisible: false,
        });
    };

    render() {
        const { PickerDate, ORDLIST, RESSECT } = this.props
        const { modalVisible, modalReport } = this.state
        const showModal = this.showModal
        const handleOk = this.handleOk
        const handleCancel = this.handleCancel
        return (
            <div style={{ marginBottom: "10px" }}>
                <div style={{ display: 'flex', alignItems: 'center', height: '40px', paddingLeft: "5px", paddingRight: "5px", background: "#cccccc" }}>
                    <t1>報告清單</t1>
                </div>
                <Nonlabviewtable PickerDate={PickerDate} ORDLIST={ORDLIST} RESSECT={RESSECT} showModal={showModal}></Nonlabviewtable>
                <Modal
                    title={"檢驗結果"}
                    visible={modalVisible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    width={1000}
                    footer={null}>
                    <InsReportModal report={modalReport}></InsReportModal>
                </Modal>
            </div>
        );
    }
}

export default Nonlabviewlayout