import React, { Component } from 'react'
import { Select } from 'antd';
import Chronicvitalchart from './Chronic_vital_chart'
import { statecolor, simulationCentermonitordata, simulationnisdata } from '../../Function/Wardstate'
import { formatxArrayhalf, formatendtime } from '../../Function/Timeformat'
import './Chronic_vital.scss'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import Crosssectionvitalsignchart from '../CrossSectionView/Crossectionvitalsignchart'


class Vitalonchronic extends Component {
    state = {
        check_state_one: 0,
        check_state_three: 0,
        check_state_oneweek: 0,
        check_state_twoweek: 0,
        datasource: null,
        statussource: "CM",
        intervalselect: this.props.select_interval,
        hrdatasource: [],
        rrdatasource: [],
        spo2datasource: [],
        abpddatasource: [],
        nbpddatasource: [],
        btdatasource: [],


    }

    componentDidMount() {
        // this.setState({
        //     datasource: this.switchdata(this.state.intervalselect, this.state.statussource)
        // })
        // this.vitalDataGet(this.props.select_interval,this.props.PickerDate)
        // console.log(this.props.select_interval,this.props.PickerDate)
    }

    vitalDataGet(InterDays, pickerdate) {
        // const simdata = vitalData.simdata
        function intervalSelect(vitalday, defaultdate) {
            if (vitalday === 0) {
                const endTime = moment(defaultdate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
                const startTime = moment(defaultdate + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
                const interval = 24
                return {
                    "endTime": endTime,
                    "startTime": startTime,
                    "interval": interval
                }
            }
            if (vitalday === 1) {
                const endTime = moment(defaultdate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
                const startTime = moment(defaultdate + (31 * 60 * 60 * 1000) - ((24 * 3 * 60 * 60 * 1000))).format("YYYYMMDDHHmmss");
                const interval = 18
                return {
                    "endTime": endTime,
                    "startTime": startTime,
                    "interval": interval
                }
            }
            if (vitalday === 2) {
                const endTime = moment(defaultdate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
                const startTime = moment(defaultdate + (31 * 60 * 60 * 1000) - ((24 * 7 * 60 * 60 * 1000))).format("YYYYMMDDHHmmss");
                const interval = 21
                return {
                    "endTime": endTime,
                    "startTime": startTime,
                    "interval": interval
                }
            }
            const endTime = moment(defaultdate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
            const startTime = moment(defaultdate + (31 * 60 * 60 * 1000) - ((24 * 14 * 60 * 60 * 1000))).format("YYYYMMDDHHmmss");
            const interval = 14
            return {
                "endTime": endTime,
                "startTime": startTime,
                "interval": interval
            }
        }
        // const { defaultdate } = this.props
        const optionvalue = intervalSelect(InterDays, pickerdate)
        // const hisid = "TEST1234"
        const hisid = this.props.caseinfo.HISID.toString().trim()
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "hr")
            .then(data => {
                this.setState({
                    hrdatasource: data.data
                })
            })
            .catch(err => {
                console.log(err);
            })
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "rr")
            .then(data => {
                this.setState({
                    rrdatasource: data.data
                })
            })
            .catch(err => {
                console.log(err);
            })
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "bt")
            .then(data => {
                this.setState({
                    btdatasource: data.data
                })
            })
            .catch(err => {
                console.log(err);
            })
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "spo2")
            .then(data => {
                this.setState({
                    spo2datasource: data.data
                })
            })
            .catch(err => {
                console.log(err);
            })
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "abpd")
            .then(data => {
                this.setState({
                    abpddatasource: data.data
                })
            })
            .catch(err => {
                console.log(err);
            })
        ApiProvider.getPatientWardValue(optionvalue.endTime, hisid, optionvalue.startTime, optionvalue.interval, "nbpd")
            .then(data => {
                this.setState({
                    nbpddatasource: data.data
                })
            })
            .catch(err => {
                console.log(err);
            })
    }
    switchdata(value_interval, sourcefrom) {
        console.log(sourcefrom)
        const { defaultdate } = this.props
        function changevitaldays(vitaldays) {
            if (vitaldays === 3) {
                return 18
            }
            else {
                return 24
            }
        }

        function Interval_switch(interval) {
            if (interval === 0) {
                return 1
            }
            if (interval === 1) {
                return 3
            }
        }
        const interval = Interval_switch(value_interval)
        const starttime = formatendtime(defaultdate) - (interval * 24 * 60 * 60 * 1000)
        if (sourcefrom === 'NIS') {
            const datatime = formatxArrayhalf(starttime, changevitaldays(interval))
            const data = simulationnisdata(datatime, changevitaldays(interval))
            console.log(data)
            return data
        }
        else {

            const datatime = formatxArrayhalf(starttime, changevitaldays(interval))
            const data = simulationCentermonitordata(datatime, changevitaldays(interval))
            return data
        }
    }
    Interval_grid_box() {
        var gridarray = []
        if (this.state.intervalselect === 0) {
            for (let index = 0; index < 24; index++) {
                const number = index + 1
                if (number === this.state.check_state_one) {
                    gridarray.push(<div style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_one(number)}></div>)
                }
                else gridarray.push(<div style={{ width: "100%", height: 360 }} onClick={() => this.onClickCheckgrid_one(number)}></div>)
            }
        }
        if (this.state.intervalselect === 1) {
            for (let index = 0; index < 18; index++) {
                const number = index + 1
                if (number === this.state.check_state_three) {
                    gridarray.push(<div style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_three(number)}></div>)
                }
                else gridarray.push(<div style={{ width: "100%", height: 360 }} onClick={() => this.onClickCheckgrid_three(number)}></div>)
            }
        }
        if (this.state.intervalselect === 2) {
            for (let index = 0; index < 21; index++) {
                const number = index + 1
                if (number === this.state.check_state_oneweek) {
                    gridarray.push(<div style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_oneweek(number)}></div>)
                }
                else gridarray.push(<div style={{ width: "100%", height: 360 }} onClick={() => this.onClickCheckgrid_oneweek(number)}></div>)
            }
        }
        if (this.state.intervalselect === 3) {
            for (let index = 0; index < 14; index++) {
                const number = index + 1
                if (number === this.state.check_state_twoweek) {
                    gridarray.push(<div style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_twoweek(number)}></div>)
                }
                else gridarray.push(<div style={{ width: "100%", height: 360 }} onClick={() => this.onClickCheckgrid_twoweek(number)}></div>)
            }
        }
        return gridarray
    }

    onClickCheckgrid_one(number) {
        this.setState(
            {
                check_state_one: number

            }
        )
    }
    onClickCheckgrid_three(number) {
        this.setState(
            {
                check_state_three: number
            }
        )
    }

    onClickCheckgrid_oneweek(number) {
        this.setState(
            {
                check_state_oneweek: number

            }
        )
    }
    onClickCheckgrid_twoweek(number) {
        this.setState(
            {
                check_state_twoweek: number
            }
        )
    }
    sourcehandleChange = (value) => {
        this.setState({
            statussource: value,
            // datasource: this.switchdata(this.state.intervalselect, value)
        })

    }
    render() {
        const { select_date } = this.props
        const { Option } = Select;
        const title_list = [{ item: "HR" }, { item: "ABP" }, { item: "NBP" }, { item: "SpO2" }, { item: "BT" }, { item: "RR" }]
        const vital_title = title_list.map((info, index) =>
            <div key={index} style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                <div>
                    <t4 style={{ display: "flex", justifyContent: "center", color: statecolor(info.item) }}>{info.item}</t4>
                    <t1 style={{ display: "flex", justifyContent: "center", color: statecolor(info.item) }}>123</t1>
                </div>
            </div>
        )
        function Interval_grid(number) {
            switch (number) {
                case 0:
                    return 24
                case 1:
                    return 18
                case 2:
                    return 21
                case 3:
                    return 14
                default:
                    return null
            }
        }
        function formatPickerTimer(pickerdate, interval) {
            if (interval === 1) {
                return {
                    startTime: pickerdate + (7 * 60 * 60 * 1000),
                    endTime: pickerdate + (31 * 60 * 60 * 1000)
                }
            }
            else {
                return {
                    startTime: pickerdate + (31 * 60 * 60 * 1000) - (3 * 24 * 60 * 60 * 1000),
                    endTime: pickerdate + (31 * 60 * 60 * 1000)
                }

            }
        }
        const width = window.screen.availWidth - 220
        const pickerTimer = formatPickerTimer(select_date,this.state.intervalselect)
        return (
            <div>
                <div style={{ display: "flex", alignItems: "center", height: "50px" }}>
                    <t1>生命徵象</t1>
                    <Select defaultValue={this.state.statussource} style={{ width: 200, marginLeft: "20px" }} onChange={this.sourcehandleChange}>
                        <Option value={'CM'}>Center Monitor</Option>
                        <Option value={'NIS'}>NIS</Option>
                    </Select>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "90px 1fr" }}>
                    <div style={{ display: "grid", gridTemplateRows: "repeat(6,60px)" }}>
                        {vital_title}
                    </div>
                    <div className='Chronicvitalchary'>
                        <div className='grid'>
                            <div style={{ display: "grid", gridTemplateColumns: "repeat(" + Interval_grid(this.state.intervalselect) + ",1fr)" }}>
                                {this.Interval_grid_box()}
                            </div>
                        </div>
                        <Crosssectionvitalsignchart
                            svg_id={"hr_svg_chronic"}
                            id_key={"hr_id_chronic"}
                            type={"hr"}
                            source={this.state.statussource}
                            startTime={pickerTimer.startTime}
                            endTime={pickerTimer.endTime}
                            height={50}
                            width={width}
                            interval={this.state.intervalselect}
                            lower={100}
                            upper={160}
                            nisData={null}
                            CenterMonitorData={null}
                            axisBot={false}
                            axisTop={true}
                            circlrcolor="#779F50"
                            colorshadow={"rgba(126, 211, 33, 0.5)"}
                            min={0}
                            max={200}>
                        </Crosssectionvitalsignchart>
                        <Crosssectionvitalsignchart
                                svg_id={"nbp_svg_chronic"}
                                id_key={"nbp_svg_chronic"}
                                type={"nbp"}
                                source={this.state.statussource}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={50}
                                width={width}
                                interval={this.state.intervalselect}
                                lower={55}
                                upper={75}
                                nisData={null}
                                CenterMonitorData={null}
                                axisBot={false}
                                axisTop={true}
                                circlrcolor="#779F50"
                                colorshadow={"rgba(126, 211, 33, 0.5)"}
                                min={0}
                                max={100}>
                            </Crosssectionvitalsignchart>
                            <Crosssectionvitalsignchart
                                svg_id={"abp_svg_chronic"}
                                id_key={"abp_svg_chronic"}
                                type={"nbp"}
                                source={this.state.statussource}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={50}
                                width={width}
                                interval={this.state.intervalselect}
                                lower={55}
                                upper={75}
                                nisData={null}
                                CenterMonitorData={null}
                                axisBot={false}
                                axisTop={true}
                                circlrcolor="#779F50"
                                colorshadow={"rgba(126, 211, 33, 0.5)"}
                                min={0}
                                max={100}>
                            </Crosssectionvitalsignchart>
                        {/* <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.hrdatasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="hr_id_chronic" svg_id="hr_svg_chronic" circlrcolor="#779F50"></Chronicvitalchart> */}
                        <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.abpddatasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="abp_id_chronic" svg_id="abp_svg_chronic" circlrcolor={"#C12528"}></Chronicvitalchart>
                        <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.nbpddatasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="nbp_id_chronic" svg_id="nbp_svg_chronic" circlrcolor={"#C12528"}></Chronicvitalchart>
                        <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.spo2datasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="spo2_id_chronic" svg_id="spo2_svg_chronic" circlrcolor={"#4773B8"}></Chronicvitalchart>
                        <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.btdatasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="bt_id_chronic" svg_id="bt_svg_chronic" circlrcolor={"#7F3DB5"}></Chronicvitalchart>
                        <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.rrdatasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="rr_id_chronic" svg_id="rr_svg_chronic" circlrcolor={"#DE7531"}></Chronicvitalchart>
                    </div>
                </div>
            </div>
        );
    }
}

export default Vitalonchronic