import React, { Component } from 'react';
import Vitalsign from './Vitalsign';
import Controlbar from '../Controlbar';
import Crossection_io from './Crossection_io'
import Labviewlayout from './Labview'
import Nonlabviewlayout from './Nonlabview'
import GAS from './GAS'
import RTbreathe from './RTbreathe'
import Prescriptionorderlayout from '../Prescriptionorder/Prescriptionorder'
import Treatmentorderlayout from '../Treatmentorder/Treatmentorder'
import Crossection_schedule from './Crosssectionschedule'
import Crosssection_todo from './Crosssection_todo'
import './Crosssectionlayout.scss'
import * as ApiProvider from '../../API/ApiProvider';
import X2JS from 'x2js';
import moment from 'moment'
import { QDC } from '../../Data/RegularData';

const SCHEDULE_TYPE_EVENT = "事件";
const timeZoneOffset = 8 * 60 * 60 * 1000;

class Crosssectionlayout extends Component {
    state = {
        Cross_item: [0, 1, 2, 3, 4, 5],

        QDCLIST: [],
        RESDGLU1: [],
        RESLAB02: [],
        RESLAB01: [],
        RESDBGAS: [],
        RESLAB0C: [],
        ORDLIST: [],
        RESSECT: [],
        QSPE: [],
        UDORDER0: [],
        TRTMNTQ1: [],

        PickerDate: moment(moment().format('YYYYMMDD'), 'YYYYMMDD').valueOf(),
        SelectInterval: 1,
        hrdatasource: [],
        rrdatasource: [],
        spo2datasource: [],
        abpdatasource: [],
        nbpdatasource: [],
        btdatasource: [],
        QDVS: [],
        QOXY: [],

        caseinfo: this.props.caseinfo,
        scheduleRawData: [],
        scheduleData: [],
        noticeRawData: [],
        startTimestamp: 0
    }
    componentDidMount() {
        this.NIS_API_IO(this.state.PickerDate)

        this.queryNotice();
        this.vitalDataGet(this.state.SelectInterval, this.state.PickerDate)
    }

    vitalDataGet(InterDays, pickerdate) {
        function intervalSelect(vitalday, defaultdate) {
            if (vitalday === 1) {
                const endTime = moment(defaultdate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
                const startTime = moment(defaultdate + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
                const interval = 0
                return {
                    "endTime": endTime,
                    "startTime": startTime,
                    "interval": interval
                }
            }
            const endTime = moment(defaultdate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
            const startTime = moment(defaultdate + (31 * 60 * 60 * 1000) - ((24 * 3) * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
            const interval = 1
            return {
                "endTime": endTime,
                "startTime": startTime,
                "interval": interval
            }
        }


        const optionvalue = intervalSelect(InterDays, pickerdate)
        const hisid = this.props.caseinfo.HISID.toString().trim()

        ApiProvider.getPatientwardvalueBoxPlot(hisid, optionvalue.startTime, optionvalue.endTime, "hr", optionvalue.interval)
            .then(data => {
                console.log(data.data)
                this.setState({
                    hrdatasource: data.data
                })
            })
            .catch(err => { console.log(err) })
        ApiProvider.getPatientwardvalueBoxPlot(hisid, optionvalue.startTime, optionvalue.endTime, "spo2", optionvalue.interval)
            .then(data => {
                console.log(data.data)
                this.setState({
                    spo2datasource: data.data
                })
            })
            .catch(err => { console.log(err) })
        ApiProvider.getPatientwardvalueBoxPlot(hisid, optionvalue.startTime, optionvalue.endTime, "rr", optionvalue.interval)
            .then(data => {
                console.log(data.data)
                this.setState({
                    rrdatasource: data.data
                })
            })
            .catch(err => { console.log(err) })


        ApiProvider.getVitalSignData(hisid, optionvalue.startTime, optionvalue.endTime, "abp")
            .then(data => {
                this.setState({
                    abpdatasource: data.data
                })
            })
            .catch(err => {
                console.log(err);
            })


        ApiProvider.getVitalSignData(hisid, optionvalue.startTime, optionvalue.endTime, "nbp")
            .then(data => {
                this.setState({
                    nbpdatasource: data.data
                })
            })
            .catch(err => {
                console.log(err);
            })
        const caseid = this.props.caseinfo.CASEID.toString().trim()
        const st = moment(optionvalue.startTime, "YYYYMMDDHHmmss").format("YYYYMMDDHHmm")
        const et = moment(optionvalue.endTime, "YYYYMMDDHHmmss").format("YYYYMMDDHHmm")
        const xm2 = new X2JS()
        ApiProvider.getQDVS(caseid, st, et)
            .then(data => {
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                this.setState({
                    QDVS: parsedata
                })
            })
            .catch(err => { console.log(err) })

        ApiProvider.getQOXY(caseid, st, et)
            .then(data => {
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                this.setState({
                    QDVS: parsedata
                })
            })
            .catch(err => { console.log(err) })

    }


    NIS_API_IO(Datetime) {
        function selectMonthInterval(Datetime) {
            const timestampInterval = new Date().getTime() - Datetime
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }
        const xm2 = new X2JS()
        const { caseinfo } = this.props
        const st = moment(Datetime + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const et = moment(Datetime + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const st_ISOstring = moment(st, 'YYYYMMDDHHmm').format('YYYY-MM-DD[T]HH:mm:ss')
        const et_ISOstring = moment(et, 'YYYYMMDDHHmm').format('YYYY-MM-DD[T]HH:mm:ss')
        const TRANSDate = moment(caseinfo.TRANSINDT, 'YYYYMMDD').format("YYYY-MM-DD")
        const MonthInterval = selectMonthInterval(Datetime)
        ApiProvider.getQDC(caseinfo.CASEID, st, et)
            .then(data => {
                console.log(xm2.xml2js(data.data))
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                this.setState(
                    { QDC: parsedata }
                )

            })
            .catch(err => {
                console.log("getQDC NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureRESDGLU1(caseinfo.HISID, TRANSDate)
            .then(data => {
                this.setState({
                    RESDGLU1: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureRESDGLU1 NG " + JSON.stringify(err));
            });
        const RESLAB02 = ApiProvider.getStoredProcedureRESLAB02(caseinfo.HISID, TRANSDate)
            .then(data => {
                this.setState({ RESLAB02: data.data })
            })
            .catch(err => {
                console.log("getStoredProcedureRESLAB02 NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureRESLAB01(caseinfo.HISID, TRANSDate)
            .then(data => {
                this.setState({
                    RESLAB01: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureRESLAB01 NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureRESDBGAS(caseinfo.HISID, TRANSDate)
            .then(data => {
                this.setState({
                    RESDBGAS: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureRESDBGAS NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureRESLAB0C(caseinfo.HISID, TRANSDate)
            .then(data => {
                this.setState({
                    RESLAB0C: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureRESLAB0C NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureORDLIST(caseinfo.HISID, TRANSDate, "ALL", "DOC3924B")
            .then(data => {
                this.setState({
                    ORDLIST: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        ApiProvider.getStoredProcedureRESSECT(caseinfo.HISID, MonthInterval, "ALL", "DOC3924B")
            .then(data => {
                this.setState({
                    RESSECT: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureRESSECT NG " + JSON.stringify(err))
            });
        ApiProvider.getQSPE(caseinfo.CASEID, st, et)
            .then(data => {
                console.log(xm2.xml2js(data.data).outmsg.resconts.rescont)
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                if (Array.isArray(parsedata)) {
                    this.setState({
                        QSPE: parsedata
                    })
                }
                else {
                    const arr = []
                    arr.push(parsedata)
                    this.setState({
                        QSPE: arr
                    })
                }
            })
            .catch(err => {
                console.log("getQSPE NG " + JSON.stringify(err))
            });
        ApiProvider.getStoredProcedureUDORDER0(caseinfo.HISID, caseinfo.CASEID, "123")
            .then(data => {
                this.setState({
                    UDORDER0: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureUDORDER0 NG " + JSON.stringify(err))
            });
        ApiProvider.getStoredProcedureTRTMNTQ1(caseinfo.HISID, caseinfo.CASEID)
            .then(data => {
                this.setState({
                    TRTMNTQ1: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureTRTMNTQ1 NG " + JSON.stringify(err))
            });
    }
    getStartEndTime() {
        const startTimestamp = moment(this.state.PickerDate).valueOf()
        const endTimesStamp = moment(startTimestamp + (24 * 60 * 60 * 1000)).valueOf()

        this.setState({
            startTimestamp: startTimestamp + (24 * 60 * 60 * 1000),
            end: endTimesStamp
        });

        return {
            startTimestamp: moment(startTimestamp).format("YYYY-MM-DD[T]HH:mm:ss"),
            endTimestamp: moment(endTimesStamp).format("YYYY-MM-DD[T]HH:mm:ss")
        };
    }

    queryNotice() {
        ApiProvider.getOnePatientNotice(this.state.caseinfo.TRANSINID, this.props.caseinfo.CASEID)
            .then(result => {
                console.log("getOnePatientNotice OK " + JSON.stringify(result));
                this.setState({
                    noticeRawData: result.data
                })
            })
            .catch(err => {
                console.log("getOnePatientNotice NG " + JSON.stringify(err));
            });
    }
    getDataFromTimestamp(timestamp) {
        console.log(timestamp)
        const dateStr = new Date(timestamp).toISOString().split('T')[0];
        const dataOfDayIndex = this.state.scheduleData.findIndex(element => element.time === dateStr);
        const dataOfDay = (dataOfDayIndex === -1) ? { scheduleList: [] } : this.state.scheduleData[dataOfDayIndex];
        return dataOfDay;
    }
    getNoticeFromTimestamp(timestamp) {
        const dateStr = new Date(timestamp).toISOString().split('T')[0];
        const noticeOfDayArray = this.state.noticeRawData.filter(element => element.time.indexOf(dateStr) > -1);
        return noticeOfDayArray;
    }

    callbackFunction = (item_array) => {
        this.setState(
            {
                Cross_item: item_array
            }
        )
    }

    callbackDateFunction = (PickerTimeStamp) => {
        this.setState({
            PickerDate: PickerTimeStamp,
            hrdatasource: [],
            rrdatasource: [],
            spo2datasource: [],
            abpddatasource: [],
            nbpddatasource: [],
            btdatasource: [],
        })
        this.NIS_API_IO(PickerTimeStamp)
        this.vitalDataGet(this.state.SelectInterval, PickerTimeStamp)
    }

    callbackDateFunctionToSelectInterval = (Interval) => {
        this.setState({
            hrdatasource: [],
            rrdatasource: [],
            spo2datasource: [],
            abpddatasource: [],
            nbpddatasource: [],
            abpsdatasource: [],
            nbpsdatasource: [],
            abpmdatasource: [],
            nbpmdatasource: [],
            btdatasource: [],
        })
        this.setState({
            SelectInterval: Interval
        })
        this.vitalDataGet(Interval, this.state.PickerDate)
    }

    display_item(number) {
        const item = this.state.Cross_item
        var filter = item.some(function (item, index, array) {
            return item === number
        });
        return filter
    }
    render() {
        const dataOfDay = this.getDataFromTimestamp(this.state.startTimestamp);
        const noticeOfDay = this.getNoticeFromTimestamp(this.state.startTimestamp);
        // console.log("noticeOfDay"+JSON.stringify(noticeOfDay));
        const crosssectioncontrollist = [
            {
                "name": "生命徵象/輸出入",
                "scrollpoint": "vitalsignIO"
            },
            {
                "name": "檢驗結果",
                "scrollpoint": "lab"
            },
            {
                "name": "呼吸評估",
                "scrollpoint": "rt"
            },
            {
                "name": "醫藥囑",
                "scrollpoint": "order"
            },
            {
                "name": "行事曆及註記",
                "scrollpoint": "schedule"
            }
        ]
        const { caseinfo } = this.props
        console.log("ORDLIST:" + this.state.ORDLIST)
        return (
            <div>
                <Controlbar caseinfo={caseinfo} PickerDate={this.state.PickerDate} parentDateCallback={this.callbackDateFunction} parentCallback={this.callbackFunction} btnlist={crosssectioncontrollist} item_array={this.state.Cross_item}></Controlbar>
                <div id="crossection" className='crossectioncontent'>
                    <div id={"vitalsignIO"} style={this.display_item(0) ? { display: "grid", gridTemplateColumns: "2fr 1fr", gridGap: "10px", marginBottom: "10px" } : { display: "none" }}>
                        <Vitalsign
                            PickerDate={this.state.PickerDate}
                            caseinfo={caseinfo}
                            defaultdate={this.state.PickerDate}
                            hrdatasource={this.state.hrdatasource}
                            rrdatasource={this.state.rrdatasource}
                            spo2datasource={this.state.spo2datasource}
                            abpdatasource={this.state.abpdatasource}
                            nbpdatasource={this.state.nbpdatasource}
                            btdatasource={this.state.btdatasource}
                            QDVS={this.state.QDVS}
                            QOXY={this.state.QOXY}
                            SelectparentDateCallback={this.callbackDateFunctionToSelectInterval} >
                        </Vitalsign>
                        <Crossection_io PickerDate={this.state.PickerDate} QDCLIST={this.state.QDCLIST} caseinfo={caseinfo} title="I/O"></Crossection_io>
                    </div>
                    <div id={"lab"} style={this.display_item(1) ? { display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" } : { display: "none" }}>
                        <Labviewlayout PickerDate={this.state.PickerDate} caseinfo={caseinfo} RESLAB01={this.state.RESLAB01} RESLAB02={this.state.RESLAB02} RESDBGAS={this.state.RESDBGAS} RESLAB0C={this.state.RESLAB0C} RESDGLU1={this.state.RESDGLU1} RESSECT={this.state.RESSECT}></Labviewlayout>
                        <Nonlabviewlayout ORDLIST={this.state.ORDLIST} RESSECT={this.state.RESSECT} caseinfo={caseinfo} PickerDate={this.state.PickerDate}></Nonlabviewlayout>
                    </div>
                    <div id={"rt"} style={this.display_item(2) ? { display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" } : { display: "none" }}>
                        <GAS caseinfo={caseinfo} PickerDate={this.state.PickerDate} searchbar={false} RESDBGAS={this.state.RESDBGAS}></GAS>
                        <RTbreathe caseinfo={caseinfo} PickerDate={this.state.PickerDate} searchbar={false} QSPE={this.state.QSPE}></RTbreathe>
                    </div>
                    <div id={"order"} style={this.display_item(3) ? { display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" } : { display: "none" }}>
                        <Prescriptionorderlayout UDORDER0={this.state.UDORDER0} caseinfo={caseinfo} searchbar={false} PickerDate={this.state.PickerDate}></Prescriptionorderlayout>
                        <Treatmentorderlayout TRTMNT01={this.state.TRTMNTQ1} caseinfo={caseinfo} searchbar={false} PickerDate={this.state.PickerDate}></Treatmentorderlayout>
                    </div>
                    <div id={"schedule"} style={this.display_item(4) ? null : { display: "none" }}>
                        <Crossection_schedule source={1} PickerDate={this.state.PickerDate} editstatus={false} caseinfo={caseinfo} noticeData={noticeOfDay} scheduleData={dataOfDay}></Crossection_schedule>
                    </div>
                    <div style={this.display_item(5) ? null : { display: "none" }}>
                        <Crosssection_todo caseinfo={caseinfo} ></Crosssection_todo>
                    </div>
                </div>
            </div>
        );
    }
}

export default Crosssectionlayout;
