function FormatNISData(qdvsvalue, qoxyvalue) {
    var HRNISLIST = []
    var RRNISLIST = []
    var SBPNISLIST = []
    var DBPNISLIST = []
    var BTNISLIST = []
    var Spo2NISLIST = []
    if (Array.isArray(qdvsvalue)) {
        for (let index = 0; index < qdvsvalue.length; index++) {
            const element = qdvsvalue[index];
            const hr = element.pluse
            const rr = element.breath
            const SBP = element.systolic
            const DBP = element.diastolic
            const BT = element.temperature
            const time = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
            HRNISLIST.push({
                Data: hr,
                Time: time
            })
            RRNISLIST.push({
                Data: rr,
                Time: time
            })
            SBPNISLIST.push({
                Data: SBP,
                Time: time
            })
            DBPNISLIST.push({
                Data: DBP,
                Time: time
            })
            BTNISLIST.push({
                Data: BT,
                Time: time
            })
        }
    }
    if (Array.isArray(qoxyvalue)) {
        for (let index = 0; index < qoxyvalue.length; index++) {
            const element = qoxyvalue[index];
            const spo2 = element.concen
            const time = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm")
            Spo2NISLIST.push({
                Data: spo2,
                Time: time

            })
        }
    }
    return ({
        HR: HRNISLIST,
        RR: RRNISLIST,
        SBP: SBPNISLIST,
        DBP: DBPNISLIST,
        BT: BTNISLIST,
        Spo2: Spo2NISLIST
    })
}

function formatPickerTimer(pickerdate, interval) {
    if (interval === 0) {
        return {
            startTime: pickerdate + (7 * 60 * 60 * 1000),
            endTime: pickerdate + (31 * 60 * 60 * 1000)
        }
    }
    else if (interval === 1) {
        return {
            startTime: pickerdate + (31 * 60 * 60 * 1000) - (3 * 24 * 60 * 60 * 1000),
            endTime: pickerdate + (31 * 60 * 60 * 1000)
        }

    }
    else if (interval === 2) {
        return {
            startTime: pickerdate + (31 * 60 * 60 * 1000) - (7 * 24 * 60 * 60 * 1000),
            endTime: pickerdate + (31 * 60 * 60 * 1000)
        }

    }
    else {
        return {
            startTime: pickerdate + (31 * 60 * 60 * 1000) - (14 * 24 * 60 * 60 * 1000),
            endTime: pickerdate + (31 * 60 * 60 * 1000)
        }
    }
}


function Interval_grid_box(intervalselect) {
    const [check_state_one, Setcheck_state_one] = useState(0)
    const [check_state_three, Setcheck_state_three] = useState(0)
    const [check_state_week, Setcheck_state_week] = useState(0)
    const [check_state_twoweek, Setcheck_state_twoweek] = useState(0)
    var gridarray = []
    if (intervalselect === 0) {
        for (let index = 0; index < 24; index++) {
            const number = index + 1
            if (number === check_state_one) {
                gridarray.push(<div style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_one(number)}></div>)
            }
            else gridarray.push(<div style={{ width: "100%", height: 360 }} onClick={(number) => Setcheck_state_one(number)}></div>)
        }
    }
    if (intervalselect === 1) {
        for (let index = 0; index < 18; index++) {
            const number = index + 1
            if (number === check_state_three) {
                gridarray.push(<div style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_three(number)}></div>)
            }
            else gridarray.push(<div style={{ width: "100%", height: 360 }} onClick={(number) => Setcheck_state_three(number)}></div>)
        }
    }
    if (intervalselect === 2) {
        for (let index = 0; index < 21; index++) {
            const number = index + 1
            if (number === check_state_week) {
                gridarray.push(<div style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_oneweek(number)}></div>)
            }
            else gridarray.push(<div style={{ width: "100%", height: 360 }} onClick={(number) => Setcheck_state_week(number)}></div>)
        }
    }
    if (intervalselect === 3) {
        for (let index = 0; index < 14; index++) {
            const number = index + 1
            if (number === check_state_twoweek) {
                gridarray.push(<div style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_twoweek(number)}></div>)
            }
            else gridarray.push(<div style={{ width: "100%", height: 360 }} onClick={(number) => Setcheck_state_twoweek(number)}></div>)
        }
    }
    return gridarray
}

