import React, { Component } from 'react';
import { Button, Select, Modal, Tabs, DatePicker, Input, Icon } from 'antd';
import Crosssectionvitalsignchart from './Crossectionvitalsignchart'
import Warningsetting from './Warningsettinglist'
import Addwarningsetting from './Addnewwaring'
import './Vitalsign.scss'
import { formatxArrayhalf, formatendtime } from '../../Function/Timeformat'
import { simulationCentermonitordata, simulationnisdata } from '../../Function/Wardstate'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'

class Vitalsign extends Component {

    state = {
        vital_days: 1, //false:一天 true:三天
        source: "CM", //false:中央站 true:NIS
        visible: false,
        visibleConflict: false,
        chackstate_one: 0,
        chackstate_three: 0,
        datasource: null,
        addNewWarningDateStr: null,
        addNewWarningHourStr: null,

        CenterMonitorValue: null
    }

    componentDidMount() {
        const startTime = moment(moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").valueOf() - (60 * 60 * 1000)).format("YYYYMMDDHHmmss")
        const endTime = moment(moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").valueOf()).format("YYYYMMDDHHmmss")
        const PHISTNUM = "TEST1234"
        this.getVitalSignData(startTime, endTime, PHISTNUM)
    }

    getVitalSignData(startTime, EndTime, Phistnum) {
        ApiProvider.getPatientwardvalueMedian(Phistnum, startTime, EndTime)
            .then(data => {
                console.log(data.data)
                this.setState({
                    CenterMonitorValue: data.data
                })
            })
            .catch(err => {
                console.log(err)
                this.setState({
                    CenterMonitorValue: null
                })
            })
    }

    onVitalCheck() {
        var gridarray = []
        if (this.state.vital_days === 3) {
            for (let index = 0; index < 18; index++) {
                const checknumber = index + 1
                if (checknumber === this.state.chackstate_three) {
                    gridarray.push(<div key={index} style={{ height: 330, width: "100%", border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_three(checknumber)}></div>)
                }
                else gridarray.push(<div key={index} style={{ height: 330, width: "100%" }} onClick={() => this.onClickCheckgrid_three(checknumber)}></div>)
            }
        }
        else {
            for (let index = 0; index < 24; index++) {
                const checknumber = index + 1
                if (checknumber === this.state.chackstate_one) {
                    gridarray.push(<div key={index} style={{ height: 330, width: "100%", border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_one(checknumber)}></div>)
                }
                else gridarray.push(<div key={index} style={{ height: 330, width: "100%" }} onClick={() => this.onClickCheckgrid_one(checknumber)}></div>)
            }
        }
        return gridarray;
    }

    onClickCheckgrid_one(number) {
        //1:7 2:8 3:9 ........
        const { defaultdate } = this.props
        const ST = moment(defaultdate).valueOf() + (31 * 60 * 60 * 1000) - (24 * 60 * 60 * 1000)
        const endtime = ST + (number * 60 * 60 * 1000)
        const starttime = endtime - 60 * 60 * 1000
        const start = moment(starttime).format("YYYYMMDDHHmmss")
        const end = moment(endtime).format("YYYYMMDDHHmmss")
        const Phistnum = "TEST1234"
        this.setState({ chackstate_one: number })
        this.getVitalSignData(start, end, Phistnum)
    }
    onClickCheckgrid_three(number) {
        console.log(number)
        const { defaultdate } = this.props
        const ST = moment(defaultdate).valueOf() + (31 * 60 * 60 * 1000) - (3 * 24 * 60 * 60 * 1000)
        const endtime = ST + ((number * 4) * 60 * 60 * 1000)
        const starttime = endtime - (4 * 60 * 60 * 1000)
        const start = moment(starttime).format("YYYYMMDDHHmmss")
        const end = moment(endtime).format("YYYYMMDDHHmmss")
        const Phistnum = "TEST1234"
        this.setState({ chackstate_three: number })
        this.getVitalSignData(start, end, Phistnum)
    }
    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleSelectDate = dataStrings => {
        //console.log("dataStrings"+dataStrings);
        this.setState({
            addNewWarningDateStr: dataStrings
        })
    }

    handleSelectHour = hourString => {
        //console.log("hourString"+hourString);
        this.setState({
            addNewWarningHourStr: hourString
        })
    }

    handleOk = e => {
        //console.log(e);
        console.log("# add warning time: " + this.state.addNewWarningDateStr + "/" + this.state.addNewWarningHourStr);
        this.setState({
            visibleConflict: true
        });
    };

    handleOkConflict = e => {
        console.log(e);
        this.setState({
            visible: false,
            visibleConflict: false
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };

    handleCancelConflict = e => {
        console.log(e);
        this.setState({
            visible: true,
            visibleConflict: false,
        });
    };

    sourcehandleChange = (value) => {
        if (value === 0) {
            this.switch_source("CM")
        }
        else {
            this.switch_source("NIS")
        }
    }


    intervalhandleChange = (value) => {

        const { defaultdate } = this.props
        // this.vitalDataGet(value, defaultdate)
        this.setState({
            vital_days: value,
            chackstate_one: 0,
            chackstate_three: 0,
            // datasource: this.switchdata(value, this.state.source)

        })
        this.props.SelectparentDateCallback(value);

    }


    switchdata(value, sourcefrom) {
        const { defaultdate } = this.props

        function changevitaldays(vitaldays) {
            if (vitaldays === 3) {
                return 18
            }
            else {
                return 24
            }
        }
        var interval
        if (value === null && value === undefined) {

            interval = this.state.vital_days
        }
        else {

            interval = value
        }
        const starttime = formatendtime(defaultdate) - (interval * 24 * 60 * 60 * 1000)
        if (sourcefrom) {
            const datatime = formatxArrayhalf(starttime, changevitaldays(interval))
            const data = simulationnisdata(datatime, changevitaldays(interval))
            return data
        }
        else {

            const datatime = formatxArrayhalf(starttime, changevitaldays(interval))
            const data = simulationCentermonitordata(datatime, changevitaldays(interval))
            return data
        }
    }


    switch_source(bool) {
        this.setState(
            {
                source: bool,
                datasource: this.switchdata(this.state.vital_days, bool)
            });
    }

    render() {
        function centervalueNull(value) {
            if (value === undefined || value === null) {
                return "--"
            }
            return value
        }
        function centervaluetoNull(value) {
            function FilterList(DataList, type) {
                const filterData = DataList.filter(function (value, index, arr) {
                    return value.type === type
                })
                if (filterData.length === 0) {
                    return null
                }
                console.log(filterData)
                return filterData[0].Mid
            }
            if (value === undefined || value === null) {
                return {
                    hr: null,
                    rr: null,
                    bt: null,
                    spo2: null,
                    abpd: null,
                    abpm: null,
                    abps: null,
                    nbpm: null,
                    nbps: null,
                    nbpd: null
                }
            }
            return {
                hr: FilterList(value, "hr"),
                rr: FilterList(value, "rr"),
                bt: FilterList(value, "bt"),
                spo2: FilterList(value, "spo2"),
                abpd: FilterList(value, "abpd"),
                abpm: FilterList(value, "abpm"),
                abps: FilterList(value, "abps"),
                nbpd: FilterList(value, "nbpd"),
                nbps: FilterList(value, "nbps"),
                nbpm: FilterList(value, "nbpm"),
            }
        }
        const { hrdatasource, rrdatasource, spo2datasource, btdatasource, abpdatasource, nbpdatasource, QDVS, QOXY } = this.props
        const { Option } = Select;
        const { TabPane } = Tabs;
        const width = window.screen.availWidth * 0.25
        const waringsettingarray = []
        for (let index = 0; index < 20; index++) {
            waringsettingarray.push(<Warningsetting key={index}></Warningsetting>)

        }
        const handleSelectDate = this.handleSelectDate;
        const handleSelectHour = this.handleSelectHour;
        const centervalue = centervaluetoNull(this.state.CenterMonitorValue)


        function vitaldaysBool(vitaldays) {
            console.log(vitaldays)
            if (vitaldays === 3) {
                return true
            }
            return false
        }


        function FormatNISData(QDVS, QOXY) {
            var HRNISLIST = []
            var RRNISLIST = []
            var SBPNISLIST = []
            var DBPNISLIST = []
            var BTNISLIST = []
            var Spo2NISLIST = []
            if (Array.isArray(QDVS)) {
                for (let index = 0; index < QDVS.length; index++) {
                    const element = QDVS[index];
                    const hr = element.pluse
                    const rr = element.breath
                    const SBP = element.systolic
                    const DBP = element.diastolic
                    const BT = element.temperature
                    const time = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
                    HRNISLIST.push({
                        Data: hr,
                        Time: time
                    })
                    RRNISLIST.push({
                        Data: rr,
                        Time: time
                    })
                    SBPNISLIST.push({
                        Data: SBP,
                        Time: time
                    })
                    DBPNISLIST.push({
                        Data: DBP,
                        Time: time
                    })
                    BTNISLIST.push({
                        Data: BT,
                        Time: time
                    })
                }
            }
            if (Array.isArray(QOXY)) {
                for (let index = 0; index < QOXY.length; index++) {
                    const element = QOXY[index];
                    const spo2 = element.concen
                    const time = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm")
                    Spo2NISLIST.push({
                        Data: spo2,
                        Time: time

                    })
                }
            }
            return ({
                HR: HRNISLIST,
                RR: RRNISLIST,
                BP: [SBPNISLIST, DBPNISLIST],
                BT: BTNISLIST,
                Spo2: Spo2NISLIST
            })
        }
        function formatPickerTimer(pickerdate, interval) {
            if (interval === 1) {
                return {
                    startTime: pickerdate + (7 * 60 * 60 * 1000),
                    endTime: pickerdate + (31 * 60 * 60 * 1000)
                }
            }
            else {
                return {
                    startTime: pickerdate + (31 * 60 * 60 * 1000) - (3 * 24 * 60 * 60 * 1000),
                    endTime: pickerdate + (31 * 60 * 60 * 1000)
                }

            }
        }
        function formatInterval(interval) {
            if (interval === 1) {
                return 0
            }
            return 1
        }
        const hrnisdata = FormatNISData(QDVS, QOXY).HR
        const rrnisdata = FormatNISData(QDVS, QOXY).RR
        const bpnisdata = FormatNISData(QDVS, QOXY).BP
        const btnisdata = FormatNISData(QDVS, QOXY).BT
        const spo2nisdata = FormatNISData(QDVS, QOXY).Spo2
        const pickerTimer = formatPickerTimer(this.props.PickerDate, this.state.vital_days)

        // console.log(hrdatasource)
        return (
            <div>
                <Modal
                    title="生命徵象警示範圍設定"
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    width={1000}>
                    <Tabs defaultActiveKey="1">
                        <TabPane tab="新增警示設定" key="1">
                            <Addwarningsetting dateCallback={handleSelectDate} hourCallback={handleSelectHour}></Addwarningsetting>
                        </TabPane>
                        <TabPane tab="警示設定清單" key="2">
                            <div style={{ maxHeight: "400px", overflow: "auto" }}>
                                {waringsettingarray}
                            </div>
                        </TabPane>
                    </Tabs>
                </Modal>
                <Modal
                    title="您設定的生效時間範圍與以下的歷史變更紀錄時間衝突，是否確定要覆蓋？"
                    visible={this.state.visibleConflict}
                    onOk={this.handleOkConflict}
                    onCancel={this.handleCancelConflict}
                    width={1000}>
                    <div style={{ display: "flex", flexDirection: "row" }}>
                        <Icon type="info-circle" style={{ color: "darkorange", width: "24px", height: "24px" }} />
                        <div style={{ marginLeft: "8px" }}>
                            <p>點選"確定"將覆蓋資料，點選"取消"回到設定頁面</p>
                            <p>1. HR：</p>
                            <p>舊設定值：2019-08-01 ~ 2019-08-11</p>
                            <p>新設定值：2019-08-01 ~ 2019-08-20</p>
                        </div>
                    </div>
                </Modal>
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', height: "50px", backgroundColor: "#cccccc", padding: "0px 5px" }}>
                    <div style={{ display: 'flex', alignItems: "center" }}>
                        <t1>Vital Sign</t1>
                        <Select defaultValue={0} style={{ width: 160, marginLeft: "16px" }} onChange={this.sourcehandleChange}>
                            <Option value={0}>Center Monitor</Option>
                            <Option value={1}>NIS</Option>
                        </Select>
                        <Select defaultValue={1} style={{ width: 90, marginLeft: "15px" }} onChange={this.intervalhandleChange}>
                            <Option value={1}>一天</Option>
                            <Option value={3}>三天</Option>
                        </Select>
                    </div>
                    {/* <Button width={90} type="primary" onClick={this.showModal}>閾值調整</Button> */}
                </div>
                <div style={{ borderRadius: "4px", border: "rgba(232,232,232,1) 1px solid", height: "400px" }}>
                    <div style={{ display: "grid", gridTemplateColumns: "80px 1fr 80px 1fr" }}>
                        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr 1fr" }}>
                            <div style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                                <div>
                                    <div style={{ fontSize: "1rem", color: "#779F50", display: "flex", justifyContent: "center" }}>HR</div>
                                    <div style={{ fontSize: "2rem", color: "#779F50", display: "flex", justifyContent: "center" }}>{centervalueNull(centervalue.hr)}</div>
                                </div>
                            </div>
                            <div style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                                <div>
                                    <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>NBP</div>
                                    <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>{centervalueNull(centervalue.nbpd)}/{centervalueNull(centervalue.nbps)}</div>
                                    <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>({centervalueNull(centervalue.nbpm)})</div>
                                </div>
                            </div>
                            <div style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                                <div>
                                    <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>ABP</div>
                                    <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>{centervalueNull(centervalue.abpd)}/{centervalueNull(centervalue.abps)}</div>
                                    <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>({centervalueNull(centervalue.abpm)})</div>
                                </div>
                            </div>
                        </div>
                        <div style={{ position: "relative" }}>
                            <div style={{ position: "absolute", top: 20, left: 50, width: width - 50, height: 330 }}>
                                <div style={{ display: "grid", gridTemplateColumns: vitaldaysBool(this.state.vital_days) ? "repeat(18, 1fr)" : "repeat(24,1fr)" }}>
                                    {this.onVitalCheck()}
                                </div>
                            </div>
                            <Crosssectionvitalsignchart
                                svg_id={"HR_svg"}
                                id_key={"HR_div"}
                                type={"hr"}
                                source={this.state.source}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={100}
                                width={width}
                                interval={formatInterval(this.state.vital_days)}
                                lower={100}
                                upper={160}
                                nisData={hrnisdata}
                                CenterMonitorData={hrdatasource}
                                axisBot={false}
                                axisTop={true}
                                circlrcolor="#779F50"
                                colorshadow={"rgba(126, 211, 33, 0.5)"}
                                min={0}
                                max={200}>
                            </Crosssectionvitalsignchart>
                            <Crosssectionvitalsignchart
                                svg_id={"NBP_svg"}
                                id_key={"NBP_div"}
                                type={"nbp"}
                                source={this.state.source}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={100}
                                width={width}
                                interval={formatInterval(this.state.vital_days)}
                                lower={55}
                                upper={75}
                                nisData={[]}
                                CenterMonitorData={nbpdatasource}
                                axisBot={false}
                                axisTop={false}
                                circlrcolor={"#C12528"}
                                colorshadow={"rgba(126, 211, 33, 0.5)"}
                                min={0}
                                max={100}>
                            </Crosssectionvitalsignchart>
                            <Crosssectionvitalsignchart
                                svg_id={"ABP_svg"}
                                id_key={"ABP_div"}
                                type={"abp"}
                                source={this.state.source}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={100}
                                width={width}
                                interval={formatInterval(this.state.vital_days)}
                                lower={55}
                                upper={75}
                                nisData={bpnisdata}
                                CenterMonitorData={abpdatasource}
                                axisBot={true}
                                axisTop={false}
                                circlrcolor={"#C12528"}
                                colorshadow={"rgba(126, 211, 33, 0.5)"}
                                min={0}
                                max={100}>
                            </Crosssectionvitalsignchart>
                        </div>
                        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr 1fr" }}>
                            <div style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                                <div>
                                    <div style={{ fontSize: "1rem", color: "#4773B8", width: "100px", display: "flex", justifyContent: "center" }}>
                                        SpO<sub style={{ bottom: "-1em" }}>2</sub>
                                    </div>
                                    <div style={{ fontSize: "2rem", color: "#4773B8", width: "100px", display: "flex", justifyContent: "center" }}>{centervalueNull(centervalue.spo2)}%</div>
                                </div>
                            </div>
                            <div style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                                <div>
                                    <div style={{ fontSize: "1rem", color: "#7F3DB5", width: "100px", display: "flex", justifyContent: "center" }}>BT</div>
                                    <div style={{ fontSize: "2rem", color: "#7F3DB5", width: "100px", display: "flex", justifyContent: "center" }}>--</div>
                                </div>
                            </div>
                            <div style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                                <div>
                                    <div style={{ fontSize: "1rem", color: "#DE7531", width: "100px", display: "flex", justifyContent: "center" }}>RR</div>
                                    <div style={{ fontSize: "2rem", color: "#DE7531", width: "100px", display: "flex", justifyContent: "center" }}>{centervalueNull(centervalue.rr)}</div>
                                </div>
                            </div>
                        </div>
                        <div style={{ position: "relative" }}>
                            <div style={{ position: "absolute", top: 20, left: 50, width: width - 50, height: 330 }}>
                                <div style={{ display: "grid", gridTemplateColumns: vitaldaysBool(this.state.vital_days) ? "repeat(18, 1fr)" : "repeat(24,1fr)" }}>
                                    {this.onVitalCheck()}
                                </div>
                            </div>
                            <Crosssectionvitalsignchart
                                svg_id={"SpO2_svg"}
                                id_key={"SpO2_div"}
                                type={"spo2"}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={100}
                                width={width}
                                interval={formatInterval(this.state.vital_days)}
                                lower={90}
                                upper={100}
                                nisData={spo2nisdata}
                                CenterMonitorData={spo2datasource}
                                axisBot={false}
                                axisTop={true}
                                circlrcolor={"#4773B8"}
                                colorshadow={"#4773B8"}
                                min={60}
                                max={100}
                                source={this.state.source}>
                            </Crosssectionvitalsignchart>
                            <Crosssectionvitalsignchart
                                svg_id={"BT_svg"}
                                id_key={"BT_div"}
                                type={"bt"}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={100}
                                width={width}
                                interval={formatInterval(this.state.vital_days)}
                                lower={36}
                                upper={38}
                                nisData={btnisdata}
                                CenterMonitorData={[]}
                                axisBot={false}
                                axisTop={false}
                                circlrcolor={"#7F3DB5"}
                                colorshadow={"rgba(126, 211, 33, 0.5)"}
                                min={33}
                                max={48}>
                            </Crosssectionvitalsignchart>
                            <Crosssectionvitalsignchart
                                svg_id={"RR_svg"}
                                id_key={"RR_div"}
                                type={"spo2"}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={100}
                                width={width}
                                interval={formatInterval(this.state.vital_days)}
                                lower={20}
                                upper={60}
                                nisData={rrnisdata}
                                CenterMonitorData={rrdatasource}
                                axisBot={true}
                                axisTop={false}
                                circlrcolor={"#DE7531"}
                                colorshadow={"rgba(126, 211, 33, 0.5)"}
                                min={0}
                                max={100}
                                source={this.state.source}>
                            </Crosssectionvitalsignchart>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}

export default Vitalsign;