import React, { Component } from 'react';
import { wardstatestringformat } from '../Function/Wardstate'
import './Wardcardstatus.scss'
import * as ApiProvider from '../API/ApiProvider'
import * as ApiGithub from '../API/ApiGithub'
import moment from 'moment';
import * as RegularData from '../Data/RegularData'

class Wardcarstatus extends Component {
    state = {
        TRTMNTQ1: [],
        UDORDER0: [],
        FlagReg: RegularData.FlagReg()
    }
    componentDidMount() {
        const { PCASENO, PHISTNUM } = this.props
        const TRQ1 = ApiProvider.getStoredProcedureTRTMNTQ1(PHISTNUM, PCASENO)
            .then(data => {
                var List = []
                const DataList = data.data
                const time = moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf()
                if (Array.isArray(DataList)) {
                    for (let index = 0; index < DataList.length; index++) {
                        const element = DataList[index];
                        const elementTime = moment(element.ORBGNDT.toString().trim(), "YYYY-MM-DD").valueOf()
                        if (elementTime === time) {
                            List.push(element)
                        }
                    }
                }
                return List
            })
            .catch(err => {

            })
        const UDORDER = ApiProvider.getStoredProcedureUDORDER0(PHISTNUM, PCASENO, "123")
            .then(data => {
                var List = []
                const DataList = data.data
                const time = moment().valueOf()
                if (Array.isArray(DataList)) {
                    for (let index = 0; index < DataList.length; index++) {
                        const element = DataList[index];
                        const st = moment(element.UDBGNDT.toString().trim(), "YYYY-MM-DD-HH.mm.ss.000000").valueOf()
                        const et = moment(element.UDENDDT.toString().trim(), "YYYY-MM-DD-HH.mm.ss.000000").valueOf()
                        if (st < time && time < et) {
                            List.push(element)
                        }
                    }
                }
                return List
            })
            .catch(err => {

            })
        const Reg = ApiGithub.getGitHubJson("FlagReg.json")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log(err)
            })
        Promise.all([TRQ1, UDORDER, Reg])
            .then(result => {
                console.log(result[0])
                this.setState({
                    TRTMNTQ1: result[0],
                    UDORDER0: result[1],
                    // FlagReg: result[2]
                })
            })
            .catch(err => {
                console.log(err)
            })
    }
    render() {
        function parseFormat(parsestring, parseJson, type) {
            if (type === "TR") {
                const dataMap = parseJson.map(function (object, index, arr) {
                    console.log(object)
                    const parsrarr = object.ParseValueTRT
                    // console.log(parsrarr)
                    if (parsrarr.length === 0) {
                        return {
                            item: object.item,
                            Status: false
                        }
                    }
                    const findvlaue = parsrarr.some(function (v, i, a) {
                        return parsestring.match(v) !== null
                    })
                    return {
                        item: object.item,
                        Status: findvlaue
                    }
                })
                return dataMap
            }
            else {
                const dataMap = parseJson.map(function (object, index, arr) {
                    const parsrarr = object.ParseValueUD
                    if (parsrarr.length === 0) {
                        return {
                            item: object.item,
                            Status: false
                        }
                    }
                    const findvlaue = parsrarr.some(function (v, i, a) {
                        return parsestring.match(v) !== null
                    })
                    return {
                        item: object.item,
                        Status: findvlaue
                    }
                })
                console.log(dataMap)
                return dataMap
            }
        }

        const TRDATA = this.state.TRTMNTQ1
        var FlagstatusList = []
        for (let index = 0; index < TRDATA.length; index++) {
            const element = TRDATA[index];
            const dataObj = parseFormat(element.ORPROCED.toString().trim(), this.state.FlagReg, "TR")
            FlagstatusList.push(dataObj)
        }
        const UDORDER = this.state.UDORDER0
        for (let index = 0; index < UDORDER.length; index++) {
            const element = UDORDER[index];
            const dataObj = parseFormat(element.UDRPNAME.toString().trim(), this.state.FlagReg, "UD")
            FlagstatusList.push(dataObj)
        }
        var status1 = false
        var status2 = false
        var status3 = false
        var status4 = false
        var status5 = false
        var status6 = false
        for (let index = 0; index < FlagstatusList.length; index++) {
            const element = FlagstatusList[index];
            if (Array.isArray(element)) {
                for (let index = 0; index < element.length; index++) {
                    const object = element[index];
                    const status = object.Status
                    switch (object.item) {
                        case "Respirator":
                            if (status) {
                                status1 = status
                            }
                            break;
                        case "TPN":
                            if (status) {
                                status2 = status
                            }
                            break;
                        case "NPO":
                            if (status) {
                                status3 = status
                            }
                            break;
                        case "Anti":
                            if (status) {
                                status4 = status
                            }
                            break;
                        case "Foley":
                            if (status) {
                                status5 = status
                            }
                            break;
                        case "Drain":
                            if (status) {
                                status6 = status
                            }
                            break;
                        default:
                            break;
                    }
                }
            }
            console.log(status1, status2, status3, status4, status5, status6)

        }
        const FlagArr = [
            { "item": "Respirator", "data": status1 },
            { "item": "TPN", "data": status2 },
            { "item": "NPO", "data": status3 },
            { "item": "Anti", "data": status4 },
            { "item": "Foley", "data": status5 },
            { "item": "Drain", "data": status6 },
        ]

        function statusspan(source) {
            if (source.item === 'Respirator') {
                return 'gridtwocoltwo'
            }
            else return 'gridtwocolone'
        }
        function style_change(source) {
            if (source.item === 'Respirator') {
                if (source.data === false) {
                    return 'statusfalse'
                }
                if (source.data === 'non-invasive') {
                    return 'statustrue'
                }
                return 'statustrue'
            }
            else {
                if (source.data) {
                    return 'statustrue'
                }
                else {
                    return 'statusfalse'
                }
            }
        }
        const flag = [
            //{ "item": "Depends", "time": 1575531000, "data": true }, // 這是呼吸？ 應該會有三態才對
            { "item": "Respirator", "time": 1575531000, "data": 'false' }, // 呼吸我先改這樣
            { "item": "TPN", "time": 1575531000, "data": true },
            { "item": "NPO", "time": 1575531000, "data": false },
            { "item": "Anti", "time": 1575531000, "data": false },
            { "item": "Foley", "time": 1575531000, "data": true },
            { "item": "Drain", "time": 1575531000, "data": false }
        ]
        const statuslist = FlagArr.map(function (info, index) {
            return (
                <div key={index} className={statusspan(info)}>
                    <t4 className={style_change(info)}>{wardstatestringformat(info)}</t4>
                </div>
            );
        })
        return (
            <div className='warscardstatusgrid'>
                {statuslist}
            </div>
        );
    }
}

export default Wardcarstatus