import React, { Component } from 'react';
import moment from 'moment';



class RTtable extends Component {

    GasListToCell = () => {
        const { DateRange, Resdbgas } = this.props
        const startTime = DateRange.st
        const endTime = DateRange.et
        //for loop filter three days
        var CellRander = []
        if (Array.isArray(Resdbgas)) {
            for (let index = 0; index < Resdbgas.length; index++) {
                const element = Resdbgas[index];
                const time = element.ORRESDT.toString() + element.ORRESTM.toString()
                const timeStamp = moment(time, 'YYYY-MM-DDHH.mm.ss').valueOf()
                if (startTime < timeStamp && timeStamp <= endTime) {
                    const PHValue = element.SS07
                    const PCO2Value = element.SS06
                    const PO2Value = element.SS08
                    const HCO3Value = element.SS04
                    const BEValue = element.SS01
                    const SampleValue = "123" //Parse
                    const O2SatValue = element.SS05
                    const FiO2Value = element.SS03
                    const ValueTime = moment(timeStamp).format("MM/DD HH:mm:ss")
                    const DataArray = [ValueTime, PHValue, PCO2Value, PO2Value, HCO3Value, BEValue, SampleValue, O2SatValue, FiO2Value]
                    const GasCell = DataArray.map(function (value, index, array) {
                        return (
                            <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                                <t3>{value}</t3>
                            </centerstyle>
                        );
                    })
                    CellRander.push(
                        <div key={index} style={{ height: "50px", display: "grid", gridTemplateColumns: "110px repeat(8,1fr)" }}>{GasCell}</div>
                    )
                }
            }
        }
        return CellRander
    }

    render() {
        const GasTitleList = ['起迄', 'PH', 'PCO2', 'PO2', 'HCO3', 'BE', 'O2', 'Sample', 'FiO2']
        const GasTitleCell = GasTitleList.map(function (value, index, array) {
            return (
                <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                    <t3>{value}</t3>
                </centerstyle>
            );
        })

        return (
            <div>
                <div style={{ height: "50px", border: "rgba(238,238,238,1) 1px solid", backgroundColor: "#E2EDF3", display: "grid", gridTemplateColumns: "110px repeat(8,1fr)" }}>
                    {GasTitleCell}
                </div>
                <div style={{ maxHeight: "400px", overflow: "auto" }}>
                    {this.GasListToCell()}
                </div>
            </div>
        );
    }
}

export default RTtable
